<template>
	<div class="liftnav" :style="'width:'+navwidth+'px'">
		<div class="liftnavcon">
			<div class="liftnav-headerimg" v-show="isCollapse==false?true:false">
				<img src="../../assets/images/logo.png" alt="">
			</div>
			<div class="liftnav-headerimgx" v-show="isCollapse==false?false:true">
				<img src="../../assets/images/logo.png" alt="">
			</div>
			<div class="liftnavconlist">
				<el-menu router :default-active="$route.path" unique-opened class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" :collapse-transition="navames">
				  <!--  -->
				  <!--  -->
				  <el-menu-item index="/index" >
					  <i class="el-icon-house"></i>
					  <span slot="title"></span>
				  </el-menu-item>
				 <!--  -->
				  <el-submenu index="2">
					<template slot="title">
					  <i class="el-icon-data-analysis"></i>
					  <span slot="title"></span>
					</template>
					<el-menu-item index="/UserAn"></el-menu-item>
					<el-menu-item index="/Menchlist"></el-menu-item>
					<el-menu-item index="/MapFb"></el-menu-item>
				  </el-submenu>
				  <!--  -->
				   <el-submenu index="3">
					<template slot="title">
					  <i class="el-icon-school"></i>
					  <span slot="title"></span>
					</template>
					<el-menu-item index="/InstList"></el-menu-item>
				   </el-submenu>
				   <!--  -->
					<el-submenu index="4">
						<template slot="title">
						  <i class="el-icon-user"></i>
						  <span slot="title"></span>
						</template>
						<el-menu-item index="/UserList"></el-menu-item>
						<!-- <el-menu-item index="/UserEval"></el-menu-item> -->
						<!-- <el-menu-item index="/TeacReal"></el-menu-item> -->
					</el-submenu>
				   <!--  -->
					<el-submenu index="5">
						<template slot="title">
						  <i class="el-icon-office-building"></i>
						  <span slot="title"></span>
						</template>
						<!-- <el-menu-item index="/CompList"></el-menu-item> -->
						<el-menu-item index="/ComplexJG"></el-menu-item>
						<!-- <el-menu-item index="/Complexroom"></el-menu-item> -->
					</el-submenu>
					<!--  -->
					<el-submenu index="6">
						<template slot="title">
						  <i class="el-icon-files"></i>
						  <span slot="title"></span>
						</template>
						<el-menu-item index="/ClassificCou"></el-menu-item>
						<el-menu-item index="/ClassificClaR"></el-menu-item>
						<el-menu-item index="/ClassificTR"></el-menu-item>
						<el-menu-item index="/ClassificCom"></el-menu-item>
					</el-submenu>
					<!--  -->
					<el-submenu index="7">
						<template slot="title">
						  <i class="el-icon-c-scale-to-original"></i>
						  <span slot="title"></span>
						</template>
						<el-menu-item index="/GMSecondkill"></el-menu-item>
					</el-submenu>
					<!--  -->
					<el-submenu index="8">
						<template slot="title">
						  <i class="el-icon-news"></i>
						  <span slot="title"></span>
						</template>
						<el-menu-item index="/HComm"></el-menu-item>
						<el-menu-item index="/HCommroom"></el-menu-item>
						<el-menu-item index="/HCommclass"></el-menu-item>
						<el-menu-item index="/HCommcourse"></el-menu-item>
						<!-- <el-menu-item index="/HCommtea"></el-menu-item> -->
					</el-submenu>
					<!--  -->
					<el-submenu index="9">
						<template slot="title">
						  <i class="el-icon-cpu"></i>
						  <span slot="title"></span>
						</template>
						<el-menu-item index="/CarouImg"></el-menu-item>
						<el-menu-item index="/IPBanner"></el-menu-item>
						<el-menu-item index="/NewRecomK"></el-menu-item>
						<el-menu-item index="/NewRecomJ"></el-menu-item>
					</el-submenu>
					<!--  -->
					<el-submenu index="10">
						<template slot="title">
						  <i class="el-icon-document-copy"></i>
						  <span slot="title"></span>
						</template>
						<el-menu-item index="/orderUnpaid"></el-menu-item>
						<el-menu-item index="/orderConduct"></el-menu-item>
						<el-menu-item index="/orderlesson"></el-menu-item>
						<el-menu-item index="/orderrefund"></el-menu-item>
						<el-menu-item index="/orderComm"></el-menu-item>
						<el-menu-item index="/orderHistory"></el-menu-item>
					</el-submenu>
					<!--  -->
					<el-submenu index="11">
						<template slot="title">
						  <i class="el-icon-money"></i>
						  <span slot="title"></span>
						</template>
						<el-menu-item index="/FinaAnal"></el-menu-item>
						<el-menu-item index="/BudgetData"></el-menu-item>
						<el-menu-item index="/MenchCashList"></el-menu-item>
						<el-menu-item index="/UserConsDeta"></el-menu-item>
					</el-submenu>
					<!--  -->
					<el-submenu index="12">
						<template slot="title">
						  <i class="el-icon-setting"></i>
						  <span slot="title"></span>
						</template>
						<el-menu-item index="/HelpCenter"></el-menu-item>
						<!-- <el-menu-item index="/PrivMana"></el-menu-item> -->
						<el-menu-item index="/SystSetup"></el-menu-item>
					</el-submenu>
				</el-menu>
			</div>
		</div>
		<!--  -->
		<div class="liftnav-button" @click="navHs()"></div>
	</div>
</template>

<script>
	export default {
	    data() {
	      return {
			thisroute:'/',
	        isCollapse: false,
			navames:false,
			navwidth:200,
			datalist:[]
	      };
	    },
	    methods: {
	      handleOpen(key, keyPath) {
	        console.log(key, keyPath);
	      },
	      handleClose(key, keyPath) {
	        console.log(key, keyPath);
	      },
		  //
		  navHs(){
			if (this.isCollapse===false) {
				this.isCollapse=true;
				this.navwidth = 64;
				this.$emit('ConHei','calc(100% - 64px)');
			} else{
				this.isCollapse=false;
				this.navwidth = 200;
				this.$emit('ConHei','calc(100% - 200px)');
			};
		  }
	    },
		mounted() {
			// console.log(this.$route.path)
		},
	  }
</script>

<style scoped lang="less">
	.el-menu{
		border:none;
		background-color:inherit;
	}
	.el-menu-vertical-demo:not(.el-menu--collapse) {
	    width: 200px;
	    min-height: 400px;
	  }
	.el-menu-item.is-active{
		color:@color2;
		background-color:@nackcolor;
	}
	@import '../../assets/style/index.less';//
	.liftnav{
		height: 100%;
		float: left;
		background-color: @navbackcolor;
		box-shadow:@navboxShadow;
		position: relative;
		.liftnavcon{
		  width: 200px;
		  height:100%;
		  overflow: hidden;
		  .liftnavconlist{
			  width: 200px;
			  height: calc(100% - 120px);
			  overflow-x: hidden;
			  overflow-y: auto;
			  padding-right: 15px;
		  }
		}
		.liftnav-button{
			position: absolute;
			width: 14px;
			height: 80px;
			background-color: @color2;
			top:50%;
			right: -14px;
			margin-top: -40px;
			border-top-right-radius: 80px;
			border-bottom-right-radius: 80px;
		}
		.liftnav-headerimg{
			width: 80px;
			height: 80px;
			background-color:@color3;
			border-radius: 50%;
			margin: 20px auto;
			img{
				display: block;
				width: 60px;
				height: 60px;
				border-radius: 50%;
				overflow: hidden;
				float: left;
				margin: 10px;
			}
		}
		.liftnav-headerimgx{
			width: 64px;
			height: 64px;
			img{
				width: 40px;
				height: 40px;
				border-radius: 50%;
				overflow: hidden;
				display: block;
				float: left;
				margin: 12px;
			}
		}
	}
	.liftnavlogo{
		background-color: @color1;
		widows: 100%;
		height:70px;
		img{
			width:50px;
			display: block;
			margin: 0 auto;
			padding-top: 10px;
		}
	}
</style>



