<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text"></span>
		</div>
		<div class="InBoxBody-con">
			<el-divider content-position="left"></el-divider>
			<el-form :inline="true" class="demo-form-inline">
			  <el-form-item :label="item.name" v-for="(item,index) in fenrunONCO" :key="index">
			    <el-input v-model="item.Proportion" placeholder=""></el-input>
			  </el-form-item>
			  <el-button type="primary" @click="onSubmit"></el-button>
			</el-form>
		    <el-divider content-position="left"></el-divider>
			<el-form :inline="true" class="demo-form-inline">
			  <el-form-item label=":">
			    <el-input v-model="minerCO.price" placeholder=" /"></el-input>
			  </el-form-item>
			  <el-button type="primary" @click="onSubmittyk"></el-button>
			</el-form>
			<el-divider content-position="left"></el-divider>
			<div>
				<el-form :inline="true" :model="formInline" class="demo-form-inline">
				  <el-form-item label=":">
				    <el-input v-model="formInline.user" placeholder=""></el-input>
				  </el-form-item>
				  <el-button type="warning" @click="SSinput"></el-button>
				</el-form>
				<el-table :data="tableData" stripe style="width: 100%" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
					<el-table-column prop="cname" label="" align="center"></el-table-column>
					<el-table-column prop="ismember" label="" align="center"></el-table-column>
					<el-table-column prop="re_num" label="" align="center"></el-table-column>
					<el-table-column prop="surplus" label="" align="center"></el-table-column>
					<el-table-column prop="give_num" label="" align="center"></el-table-column>
					<el-table-column label="" width="150"  align="center">
					  <template slot-scope="scope" >
						<el-button
						  size="mini"
						  type="success"
						  @click="handleEdit(scope.$index, scope.row)"></el-button>
					  </template>
					</el-table-column>
				</el-table>
				<div class="block pagination">
				   <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
					:current-page.sync="form.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
					</el-pagination>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
	    data() {
	      return {
			tableData:[],
			minerCO:'',
			num:0,
			fenrunONCO:[],
	        formInline: {
	          user: '',
	          region: '',
	        },
			form:{
				page:1
			}
	      }
	    },
	    methods: {
		  // 
	      onSubmit() {
			let that =this;
			let data=that.fenrunONCO;
			that.$post('v1/setpcShareBenefit',data).then((response) => {
				that.$message({
					type: 'success',
					message: '!'
				});
			});
	      },
		  // 
		  onSubmit() {
			let that =this;
			let data=that.minerCO;
			that.$post('v1/setUserPrice',data).then((response) => {
				that.$message({
					type: 'success',
					message: '!'
				});
			});
		  },
		  //
		  onSubmittyk(){
			  
		  },
		  //
		  SSinput(){
			  
		  },
		  //
		  handleEdit(index,row){
			  this.$prompt('', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					inputPattern:'',
					inputType:'number',
					inputErrorMessage: ''
				  }).then(({ value }) => {
					console.log(value)
				  }).catch(() => {
					this.$message({
					  type: 'info',
					  message: ''
					});       
				  });
			  },
		  // 
		  handleSizeChange(val) {
		  	this.form.page = val;
			 this.lietcon();
		  },
		  handleCurrentChange(val) {
		  	this.form.page = val;
			this.lietcon();
		  },
		  // 
		  fenrunOne(){
			  let that =this;
			  let data={};
			  that.$post('v1/getpcShareBenefit',data).then((response) => {
			  	that.fenrunONCO = response.data;
			  });
		  },
		  // 
		  miner(){
			  let that =this;
			  let data={};
			  that.$post('v1/getUserPrice',data).then((response) => {
				that.minerCO = response.data;
			  });
		  },
		  // 
		  lietcon(){
			  let that =this;
			  let data=that.form;
			  that.$post('v1/getpcRechargeList',data).then((response) => {
				  console.log(response.data);
				  if (response.code==1000) {
				  	response.data.info.forEach(obj=>{
				  		obj.ismember = that.statepay(obj.ismember);
				  	});
				  	that.tableData = response.data.info;
				  	that.num = response.data.num;
				  }else{
					  
				  }
			  });
		  },
		  //
		  statepay(e){
			  if (e==1) {
			  	e=""
			  } else{
			  	e=""
			  };
			  return e
		  }
	    },
		mounted() {
			this.fenrunOne();
			this.miner();
			this.lietcon();
		}
	  }
</script>

<style scoped lang="less">
	@import '../../assets/style/index.less';//
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
		}
		.InBoxBody-con{
			width:calc(100% - 80px);
			padding: 0 40px;
		}
	}
	.pagination{
		float: right;
		margin: 20px;
	}
</style>



