<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
			  <el-breadcrumb-item :to="{ path: '/InstList' }"></el-breadcrumb-item>
			  <el-breadcrumb-item :to="{ path: '/InstList/instdetl',query:{id:$route.query.id} }"></el-breadcrumb-item>
			  <el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span></span>
				<el-form ref="form" :model="form" label-width="80px">
					<el-input class="input" v-model="form.name" placeholder=""></el-input>
					<el-select class="input" v-model="form.cg_id" clearable  placeholder="">
					    <el-option
					      v-for="item in optionskm"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
					<el-select class="input" v-model="form.course_status" clearable  placeholder="">
					    <el-option
					      v-for="item in optionsnl"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
					<el-select class="input" v-model="form.type" clearable  placeholder="">
					    <el-option
					      v-for="item in optionsSX"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
					<el-button class="time" type="warning" plain @click="Search">   </el-button>
					<el-button class="buttonRight" round @click="handleEdit('1')">  </el-button>
				</el-form>
			</div>
			<!--  -->
			 <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark"  style="width: 100%" stripe :header-cell-style="{background:'#eef1f6',color:'#606266'}">
				<el-table-column prop="cuname" label="" align="center" fixed></el-table-column>
				<el-table-column prop="clname" label="" align="center"></el-table-column>
				<el-table-column prop="caname" label="" align="center"></el-table-column>
				<el-table-column prop="c_num" label="" align="center"></el-table-column>
				<el-table-column prop="Zprice" label="" align="center"></el-table-column>
				<el-table-column prop="present_price" label="" align="center"></el-table-column>
				<el-table-column prop="stateCou" label="" align="center"></el-table-column>
				<el-table-column label="" width="360"  align="center" fixed="right">
				  <template slot-scope="scope" >
					<el-button
						size="mini"
						type="primary"
						v-if="scope.row.course_status!=2"
						@click="handledetails(scope.$index, scope.row)"></el-button>
					<el-button
					  size="mini"
					   type="warning"
					   v-if="scope.row.course_status!=2"
					  @click="handleEdit('2',scope.$index, scope.row)"></el-button>
					<el-button
					  size="mini"
					  v-if="scope.row.course_status!=2"
					  type="danger"
					  v-show="scope.row.type==1?true:false"
					  @click="handleSX('2',scope.$index, scope.row)"></el-button>
					<el-button
					  size="mini"
					  v-if="scope.row.course_status!=2"
					  type="success"
					  v-show="scope.row.type==2?true:false"
					  @click="handleSX('1',scope.$index, scope.row)"></el-button>
					<el-button
					  size="mini"
					  v-if="scope.row.course_status!=2"
					  @click="handleDelete(scope.$index, scope.row)"></el-button>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="form.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
				</el-pagination>
			 </div>
		</div>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  optionskm: [],
		  optionsnl:[
			  {id:'',name:''},
			  {id:2,name:''},
			  {id:1,name:''},
			  {id:3,name:''},
		  ],
		  optionsSX:[
			  {id:'',name:''},
			  {id:1,name:''},
			  {id:2,name:''},
		  ],
		  value: '',
		  //
	      form:{
			name:'',
			cg_id:'',
			type:'',
			course_status:'',
			page:1
		  },
		  // 
		  num:0,
		  tableData: [],
	    }
	  },
	  methods: {
		// 
		handleEdit(state,index, row){
			// console.log(index, row);
			this.$storeState.coursemanagement = this.form;
			if (state!='2') {
				this.$router.push({path:'coursemanagement/CourGemCon',query:{id:this.$route.query.id}});
			} else{
				this.$router.push({path:'coursemanagement/CourGemCon',query:{mid:row.id,id:this.$route.query.id}});
			}
		},
		//
		handledetails(index, row) {
			console.log(index, row);
			this.$router.push({path:'coursemanagement/CourGeD',query:{mid:row.id,id:this.$route.query.id}});
		},
		// 
		Search(){
			this.form.page = 1;
			this.XSinfo();
		},
		handleSizeChange(val) {
			console.log(` ${val} `);
			this.form.page = val;
			this.XSinfo();
		},
		handleCurrentChange(val) {
			console.log(`: ${val}`);
			this.form.page = val;
			this.XSinfo();
		},
		//
		handleDelete(index, row) {
			this.$confirm(''+row.cuname +', ?', '', {
			  confirmButtonText: '',
			  cancelButtonText: '',
			  type: 'warning'
			}).then(() => {
				let that =this;
				let data = {
					course_id:row.id
				};
				that.$post('v1/delpcCourse',data).then((response) => {
					console.log('',response);
					if (response.code==1000) {
						this.XSinfo();
					};
				});  
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: ''
			  });          
			});
		},
		//
		handleSX(tapd,index, row) {
			let textname = '';
			if (tapd==2) {
				textname = ''
			};
			this.$confirm(textname+row.cuname +', ?', '', {
			  confirmButtonText: '',
			  cancelButtonText: '',
			  type: 'warning'
			}).then(() => {
				let that =this;
				let data = {
					course_id:row.id,
					type:tapd
				};
				that.$post('v1/editpcCourseType',data).then((response) => {
					console.log('',response);
					if (response.code==1000) {
						this.XSinfo();
					};
				});  
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: ''
			  });          
			});
		},
		// 
		XSinfo(){
			let that =this;
			let data=this.form;
			data.mem_id = this.$route.query.id;
			that.$post('v1/getpcCourse',data).then((response) => {
				console.log('',response);
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						obj.Zprice = (obj.present_price/obj.c_num).toFixed(2);
						obj.stateCou = obj.type==1?'':''
					});
					that.tableData = response.data.info;
					that.num = response.data.num;
				};
				if (response.error_code==10000) {
					that.tableData=[]
				};
			});
		},
		// null-
		moenystate(e){
			if(e==null){e='-'}
			return e
		},
		//
		objKm(){
			let that =this;
			that.$post('v1/getpcCategory').then((response) => {
				console.log('',response);
				if (response.code==1000) {
					this.optionskm = response.data.info
				};
			});
		}
	  },
	  mounted() {
	  	this.XSinfo();
		this.objKm();
	  },
	  created(){
		//
		if (this.$storeState.coursemanagement!=null) {
			this.form = this.$storeState.coursemanagement;
		};  
	  }
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
					margin-bottom: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
					margin-bottom: 12px;
				}
				.buttonRight{
					float: right;
					margin-right: 30px;
				}
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width: 50px;
				height: 50px;
				display: block;
				margin: 0 auto;
			}
		}
	}
</style>



