<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/HCommclass' }"></el-breadcrumb-item>
				<el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-divider content-position="left"></el-divider>
		<div class="InBoxBody-con">
			<el-form :model="ruleForm" label-position="top" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
				<el-form-item  label="" prop="name" style="width:calc(100% - 80px);">
					<el-input size="large" v-model="ruleForm.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="community_id" style="width:calc(100% - 80px);">
					<el-select style="width: 100%;" size="large" v-model="ruleForm.community_id" placeholder="">
						<el-option v-for="(item,index) in classShequ" :key="index" :label="item.name" :value="item.id"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item class="inupdbox" label="" prop="valkm">
					<el-cascader
					    size="large"
						style="width: 100%;"
					    v-model="ruleForm.valkm"
					    :options="optionskm"
					    :props="{ expandTrigger: 'hover' }">
					</el-cascader>
				</el-form-item>
				<el-form-item class="inupdbox" label="" prop="valnl">
					<el-cascader
					    size="large"
						style="width: 100%;"
					    v-model="ruleForm.valnl"
					    :options="optionsnl"
					    :props="{expandTrigger: 'hover'}">
					</el-cascader>
				</el-form-item>
				<el-form-item label="" prop="title" style="width:calc(100% - 80px);">
					<el-input type="textarea" :autosize="textst" v-model="ruleForm.title" placeholder="200"></el-input>
				</el-form-item>
				<el-form-item label="(,400*400,2M)" prop="wheel_img">
					<el-upload multiple action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.wheel_img"
					 :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
						<img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>
				<el-form-item label="" prop="details" style="width:calc(100% - 80px);">
					<Editor :serverUrl="imgUrl" :imgData="imgData" :detailCon="detailContent" @changeContent="changeContent" ref="editor"></Editor>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
					<el-button @click="resetForm('ruleForm')"></el-button>
					<el-button @click="$router.go(-1)"></el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
	import Editor from '../../../assets/Plugin/RichText.vue';
	export default {
		components: {
			Editor
		},
		data() {
			return {
				classShequ:[],
				detailContent:'',
				//
				optionskm:[],
				optionsnl:[],
				//
				dialogImageUrl: '',
				dialogVisible: false,
				// 
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {
					wheel_img:[],// 
					valkm:'',
					valnl:'',
					name:'', //
					title:'',// 
					details:'',// 
					community_id:[]
				},
				rules: {
					name: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					valkm: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					valnl: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					community_id: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					title: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					details: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					wheel_img: [{
						required: true,
						message: '',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			// 
			changeContent(value){
				this.ruleForm.details = value;
			},
			//
			handleRemove(file, fileList) {
				console.log(file, fileList);
				this.ruleForm.wheel_img = fileList;
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
				console.log(file);
			},
			// 
			handsuccess(url,file, fileList) {
				console.log('', file, fileList);
				this.ruleForm.wheel_img = fileList;
			},
			//
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let that = this;
						// 
						let data = that.ruleForm;
						let tjurl = 'v1/addpcCommunityCurriculum';
						if (that.$route.query.mid != undefined) {
							tjurl = 'v1/setpcCommunityCurriculum';
							data.course_id = that.$route.query.mid;
						};
						that.$post(tjurl, data).then((response) => {
							console.log('', response);
							if (response.code == 1000) {
								that.$message({
									message: '',
									type: 'success'
								});
								that.$router.go(-1);
							};
						})
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			//
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			// 
			//
			objKm(){
				let that =this;
				that.$post('v1/getpcgroupCategory').then((response) => {
					// console.log('',response);
					if (response.code==1000) {
						this.optionskm = response.data
					};
				});
			},
			//
			objNL(){
				let that =this;
				that.$post('v1/getpcgroupStudyType').then((response) => {
					// console.log('',response);
					if (response.code==1000) {
						this.optionsnl = response.data
					};
				});
			},
			//
			shequobj() {
				let that = this;
				let data = {};
				that.$post('v1/getpcCommunityNameType', data).then((response) => {
					// console.log('',response);
					if (response.code == 1000) {
						that.classShequ = response.data
					};
				});
			},
			// 
			ObjectCon(){
				let that =this;
				let data = {
					course_id:this.$route.query.mid
				};
				that.$post('v1/getpcCommunityCurriculumdetails',data).then((response) => {
					console.log('',response);
					if (response.code==1000) {
						this.ruleForm = response.data;
						this.detailContent = response.data.details;
					};
				});
			}
		},
		mounted() {
			this.objKm();
			this.objNL();
			this.shequobj();
			//id
			if (this.$route.query.mid != undefined) {
				this.ObjectCon();
			};
		},
		computed:{
			//
			imgUrl(){
				return 'https://zht.insooner.com/jg/v1/getUpdatePictures';   // 
			},
			imgData(){
				return {}  //
			},
		},
		 watch:{
		   //
		   // editorgetContent
		   //   content(newVal,oldVal){
			//    this.$refs.editor.getContent(newVal);
			// }
		},
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: calc(100% - 80px);
			padding-left: 80px;

			.inupdbox {
				width: calc(50% - 80px);
				padding-right: 80px;
				float: left;
			}
		}
	}
</style>



