export function MoneyYZ(e) {
	var reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
	if (e!= "" && !reg.test(e)) {
		e = "";
	};
	return e
};
//
export function mudYZ(e) {
	var reg = /[^\d]/g;
	if (e!= "" && reg.test(e)) {
		e = "";
	};
	return e
};



