<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text"></span>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span></span>
				<el-form ref="form" :model="form" label-width="80px">
					<el-input class="input" v-model="form.cname" placeholder=""></el-input>
					<div class="block time">
					  <el-date-picker
						 v-model="form.time"
						  type="daterange"
						  align="right"
						  value-format="timestamp"
						  unlink-panels
						  range-separator=""
						  start-placeholder=""
						  end-placeholder=""
						  :picker-options="pickerOptions">
					  </el-date-picker>
					</div>
					<el-select style="float: left;margin-left: 12px;" clearable v-model="form.is_verification" placeholder="">
					  <el-option label="" value="2"></el-option>
					  <el-option label="" value="3"></el-option>
					  <el-option label="" value="4"></el-option>
					  <el-option label="" value="1"></el-option>
					</el-select>
					<el-button class="time" type="warning" plain @click="Search">   </el-button>
				</el-form>
			</div>
			<!--  -->
			 <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark"  style="width: 100%" stripe  
			 @selection-change="handleSelectionChange" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
			    <el-table-column type="selection" width="55"></el-table-column>
				<el-table-column prop="uid" label="" align="center"></el-table-column>
				<el-table-column prop="cname" label="" align="center"></el-table-column>
				<el-table-column prop="nickname" label="" align="center"></el-table-column>
				<el-table-column prop="create_time" label="" align="center"></el-table-column>
				<el-table-column prop="phone" label="" align="center"></el-table-column>
				<el-table-column prop="is_verification" label="" align="center"></el-table-column>
				<el-table-column prop="cumulative_price" label="" align="center"></el-table-column>
			    <el-table-column prop="cumulative_retreat_price" label=""  align="center"></el-table-column>
				<el-table-column label="" width="250"  align="center">
				  <template slot-scope="scope" >
					<el-button
					  size="mini"
					  type="success"
					  v-if="scope.row.is_verification==''"
					  @click="handleEdit(scope.$index, scope.row,'1')"></el-button>
					<el-button
						size="mini"
						type="danger"
						v-if="scope.row.is_verification==''"
						@click="handleEdit(scope.$index, scope.row,'2')"></el-button>
					<el-button
					  size="mini"
					  type="warning"
					  @click="handleDelete(scope.$index, scope.row)"></el-button>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="form.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
				</el-pagination>
			 </div>
		</div>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  num:0,//
		  form:{
			cname:'',
			start_time:'',
			is_verification:'3',
			page:1
		  },
		  textarea: '',
		  dialogVisible: false,
		  pickerOptions: {
			shortcuts: [{
			  text: '',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
				picker.$emit('pick', [start, end]);
			  }
			}, {
			  text: '',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
				picker.$emit('pick', [start, end]);
			  }
			}, {
			  text: '',
			  onClick(picker) {
				const end = new Date();
				const start = new Date();
				start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
				picker.$emit('pick', [start, end]);
			  }
			}]
		  },
		  tableData: [],
		  multipleSelection: []
	    }
	  },
	  methods: {
		//
		Search(){
			this.form.page=1;
			this.ObjList();
		},
		toggleSelection(rows) {
		  if (rows) {
			rows.forEach(row => {
			  this.$refs.multipleTable.toggleRowSelection(row);
			});
		  } else {
			this.$refs.multipleTable.clearSelection();
		  }
		},
		handleSelectionChange(val) {
		  this.multipleSelection = val;
		},
		// 
		handleSizeChange(val) {
			this.form.page = val;
			this.ObjList();
		},
		handleCurrentChange(val) {
			this.form.page = val;
			this.ObjList();
		},
		//
		handleEdit(index, row,e) {
			console.log(index, row,e);
			let msg = '',statetyp='',that =this
			if (e==1) {
				msg = ''+row.cname+'';
				statetyp = '1';
			} else{
				msg = ''+row.cname+'';
				statetyp = '4';
			}
			this.$confirm(msg, '', {
			  confirmButtonText: '',
			  cancelButtonText: '',
			  type: 'warning'
			}).then(() => {
			  let data={
				  is_verification:statetyp,
				  mem_id:row.uid
			  };
			  that.$post('v1/updateMemberVerification',data).then((response) => {
			  	console.log('',response);
			  	that.$message({
					type: 'success',
					message: '!'
			  	});
				that.ObjList();
			  });
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: ''
			  });          
			});
		},
		//
		handleDelete(index, row) {
			console.log(index, row);
			//
			this.$storeState.InstListNav = this.form;
			this.$router.push({path:'/InstList/instdetl',query:{id:row.uid}});
		},
		handleClose(done) {
		this.$confirm('')
		  .then(_ => {
			done();
		  })
		  .catch(_ => {});
		},
		//
		ObjList(){
			let that =this;
			let data=this.form;
			that.$post('v1/getzhtMemberlist',data).then((response) => {
				console.log('',response);
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						obj.create_time = that.$timeZH(obj.create_time);
						obj.is_verification = that.statepay(obj.is_verification);
						obj.cumulative_price = that.moenystate(obj.cumulative_price);
						obj.cumulative_retreat_price = that.moenystate(obj.cumulative_retreat_price);
					});
					that.tableData = response.data.info;
					that.num = response.data.num;
				};
				if (response.error_code==10000) {
					that.tableData=[]
				};
			});
		},
		// 
		statepay(e){
			let namer = '';
			if (e==1) {
				namer = ''
			} else if(e==2){
				namer = ''
			} else if(e==3){
				namer = ''
			} else if(e==4){
				namer = ''
			};
			return namer
		},
		// null-
		moenystate(e){
			if(e==null){e='-'}
			return e
		}
	  },
	  mounted() {
	  	this.ObjList();
	  },
	  created(){
		//
		if (this.$storeState.InstListNav!=null) {
			this.form = this.$storeState.InstListNav;
		};  
	  }
	}
</script>

<style scoped lang="less">
	@import '../../assets/style/index.less';//
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
				}
				.buttonRight{
					float: right;
					margin-right: 30px;
				}
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width: 50px;
				height: 50px;
				display: block;
				margin: 0 auto;
			}
		}
	}
</style>



