<template>
	<div style="overflow: hidden;">
		<el-table :data="tableData" style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
			<el-table-column prop="Ranking" align="center" label="">
			</el-table-column>
			<el-table-column prop="name" align="center" label="">
			</el-table-column>
			<el-table-column prop="date" label="" align="center" sortable width="180">
			</el-table-column>
			<el-table-column prop="datesr" label="" align="center" sortable width="180">
			</el-table-column>
			<el-table-column prop="datesr" label="" align="center" sortable width="180">
			</el-table-column>
			<el-table-column prop="datesr" label="" align="center" sortable width="180">
			</el-table-column>
			<el-table-column prop="datesr" label="" align="center" sortable width="180">
			</el-table-column>
		</el-table>
		<el-pagination style="float: right;margin: 30px 30px 0 0;" @size-change="handleSizeChange" @current-change="handleCurrentChange"
		 :current-page.sync="currentPage" :page-size="100" layout="prev, pager, next, jumper" :total="1000">
		</el-pagination>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				tableData: [{
						Ranking: '1',
						date: '12',
						datesr: '8000',
						name: 'XX',
						address: ' 1518 ',
					},
					{
						Ranking: '1',
						date: '12',
						datesr: '8000',
						name: 'XX',
						address: ' 1518 ',
					},
					{
						Ranking: '1',
						date: '12',
						datesr: '8000',
						name: 'XX',
						address: ' 1518 ',
					},
					{
						Ranking: '1',
						date: '12',
						datesr: '8000',
						name: 'XX',
						address: ' 1518 ',
					}
				],
				currentPage: 1
			}
		},
		methods: {
			formatter(row, column) {
				return row.address;
			},
			handleSizeChange(val) {
				console.log(` ${val} `);
			},
			handleCurrentChange(val) {
				console.log(`: ${val}`);
			}
		}
	}
</script>



