<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
			  <el-breadcrumb-item :to="{ path: '/InstList' }"></el-breadcrumb-item>
			  <el-breadcrumb-item :to="{ path: '/InstList/instdetl',query:{id:$route.query.id} }"></el-breadcrumb-item>
			  <el-breadcrumb-item :to="{ path: '/InstList/instdetl/TeacherAdmin',query:{id:$route.query.id} }"></el-breadcrumb-item>
			  <el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con-tit"></div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<el-form :model="ruleForm" :rules="rules" label-position="top" ref="ruleForm" label-width="100px" class="demo-ruleForm">
					<el-form-item label="" prop="name">
						<el-input size="large" v-model="ruleForm.name" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="type_id">
						<el-select style="width: 100%;" size="large" v-model="ruleForm.type_id" placeholder="">
							<el-option v-for="(item,index) in classFL" :key="index" :label="item.name" :value="item.id"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label=":" prop="brief">
						<el-input type="textarea" :autosize="textst" v-model="ruleForm.brief" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="(400*400,1M):" prop="img">
						<el-upload action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.img"
						 :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove">
							<i class="el-icon-plus"></i>
						</el-upload>
						<el-dialog :visible.sync="dialogVisible">
							<img width="100%" :src="dialogImageUrl" alt="">
						</el-dialog>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
						<el-button @click="resetForm('ruleForm')"></el-button>
						<el-button @click="returnfomr('ruleForm')"></el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				//
				classFL: [],
				dialogImageUrl: '',
				dialogVisible: false,
				// 
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {
					name: '', //
					brief: '', //
					img: [],
					type_id: []
				},
				rules: {
					name: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					type_id: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					brief: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					img: [{
						required: true,
						message: '',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			//
			handleRemove(file, fileList) {
				console.log(file, fileList);
				this.ruleForm.img = fileList;
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
				console.log(file);
			},
			// 
			handsuccess(file, fileList) {
				console.log('', file, fileList);
				this.ruleForm.img.push(fileList);
			},
			//
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let that = this;
						// 
						let data = that.ruleForm;
						let tjurl = 'v1/addpcTeacher';
						if (that.$route.query.Teachid != undefined) {
							tjurl = 'v1/setpcTeacher';
							data.tea_id = that.$route.query.Teachid;
						}else{
							data.mem_id = that.$route.query.id; 
						};
						that.$post(tjurl, data).then((response) => {
							console.log('', response);
							if (response.code == 1000) {
								that.$message({
									message: '',
									type: 'success'
								});
								that.$router.go(-1);
							};
						})
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			returnfomr() {
				this.$router.go(-1);
			},
			ObjectCon() {
				let that = this;
				let data = {
					tea_id: this.$route.query.Teachid
				};
				that.$post('v1/getpcTeacherdetails', data).then((response) => {
					console.log('', response);
					if (response.code == 1000) {
						that.ruleForm = response.data
					};
				});
			},
			// 
			fenobj() {
				let that = this;
				let data = {};
				that.$post('v1/getpcTeacherTypelist', data).then((response) => {
					// console.log('',response);
					if (response.code == 1000) {
						that.classFL = response.data
					};
				});
			}
		},
		mounted() {
			this.fenobj();
			//id
			if (this.$route.query.Teachid != undefined) {
				this.ObjectCon();
			};
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con-tit {
			padding: 20px;
			color: @color4;
			border-bottom: @borderbox;
		}

		.InBoxBody-con {
			width: calc(100% - 200px);
			padding-left: 50px;

			.InBoxBody-con-input {
				width: 100%;
				overflow: hidden;
				padding: 0 0 20px 0;
				margin-top: 40px;

				.InBoxBody-con-input-style {
					width: calc(100% - 40px);
					padding: 20px 20px 0 20px;
					overflow: hidden;

					.InBoxBody-con-input-tit {
						float: left;
						line-height: 32px;
						font-size: 14px;
						width: 90px;
					}

					.flat {
						float: left;
						width: 200px;
						margin-right: 40px;
					}

					.flattextarea {
						float: left;
						width: 70%;
						margin-right: 40px;
					}
				}
			}
		}
	}
</style>



