// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App'
import router from './router'

// 
import { provinceAndCityData, regionData, provinceAndCityDataPlus, regionDataPlus, CodeToText, TextToCode } from 'element-china-area-data'
Vue.prototype.$regionData=regionData;
Vue.prototype.$CodeToText=CodeToText;

// axios
import axios from 'axios'
import {post,fetch,patch,put} from './utils/requesthttp'
//
Vue.prototype.$post=post;
Vue.prototype.$fetch=fetch;
Vue.prototype.$patch=patch;
Vue.prototype.$put=put;
//echarts
import * as echarts from 'echarts';

//ElementUI
Vue.use(ElementUI, { size: 'small', zIndex: 3000 });
Vue.config.productionTip = false
//
import store from './store/state.js' //
// 
Vue.prototype.$timeZH= store.timeZH;//
Vue.prototype.$storeState= store.store.state;//vuex	

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,//vuex
  components: { App },
  template: '<App/>'
})



