<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/InstList' }"></el-breadcrumb-item>
				<el-breadcrumb-item :to="{ path: '/InstList/instdetl',query:{id:$route.query.id} }"></el-breadcrumb-item>
				<el-breadcrumb-item :to="{ path: '/InstList/instdetl/Classroom',query:{id:$route.query.id} }"></el-breadcrumb-item>
				<el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con-tit"></div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<el-form :model="ruleForm" :rules="rules" label-position="top" ref="ruleForm" label-width="100px" class="demo-ruleForm">
					<el-form-item label="" prop="name">
						<el-input size="large" v-model="ruleForm.name" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="selectedOptions">
						<el-cascader style="width: 100%;" size="large" :options="$regionData" :placeholder="ruleForm.province?ruleForm.province+'/'+ruleForm.city+'/'+ruleForm.area:''" v-model="ruleForm.selectedOptions" @change="addressChange">
						</el-cascader>
					</el-form-item>
					<el-form-item label="" prop="address">
						<el-input size="large" v-model="ruleForm.address" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="type_id">
					    <el-select style="width: 100%;" size="large" v-model="ruleForm.type_id" placeholder="">
					      <el-option v-for="(item,index) in classFL" :key="index" :label="item.name" :value="item.id"></el-option>
					    </el-select>
					  </el-form-item>
					<el-form-item label=":" prop="brief">
						<el-input type="textarea" :autosize="textst" v-model="ruleForm.brief" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="(400*400,1M):" prop="img">
						<el-upload
						  action="https://zht.insooner.com/jg/v1/getUpdatePictures"
						  :limit="1"
						  list-type="picture-card"
						  :file-list="ruleForm.img"
						  :on-preview="handlePictureCardPreview"
						  :on-success="handsuccess"
						  :on-remove="handleRemove">
						  <i class="el-icon-plus"></i>
						</el-upload>
						<el-dialog :visible.sync="dialogVisible">
						  <img width="100%" :src="dialogImageUrl" alt="">
						</el-dialog>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
						<el-button @click="resetForm('ruleForm')"></el-button>
						<el-button @click="returnfomr('ruleForm')"></el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				//
				classFL:[],
				dialogImageUrl: '',
				dialogVisible: false,
				// 
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {
					name:'', //
					province:'', //
					city:'', //
					area:'', //
					address:'', //
					brief:'', //
					img:[],
					longitude:'',
					latitude:'',
					selectedOptions: [],
					type_id:[]
				},
				rules: {
					name: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					province: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					address: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					type_id: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					brief: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					img: [{
						required: true,
						message: '',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			// 
			addressChange(arr) {
				console.log(arr);
				console.log(this.$CodeToText[arr[0]], this.$CodeToText[arr[1]], this.$CodeToText[arr[2]]);
				this.ruleForm.province = this.$CodeToText[arr[0]];
				this.ruleForm.city = this.$CodeToText[arr[1]];
				this.ruleForm.area = this.$CodeToText[arr[2]];
			},
			//
			handleRemove(file, fileList) {
				console.log(file, fileList);
				this.ruleForm.img = fileList;
			},
			handlePictureCardPreview(file) {
			  this.dialogImageUrl = file.url;
			  this.dialogVisible = true;
			  console.log(file);
			},
			// 
			handsuccess(file, fileList){
				console.log('',file, fileList);
				this.ruleForm.img.push(fileList);
			},
			//
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let that =this;
						var geocoder, map, marker = null;
						geocoder = new qq.maps.Geocoder();
						var address = that.ruleForm.province+that.ruleForm.city+that.ruleForm.area+that.ruleForm.address;    
			      geocoder.getLocation(address); //
			      //
			      geocoder.setComplete(function(result) {
			         console.log(result.detail.location);
						   that.ruleForm.latitude = result.detail.location.lat;
						   that.ruleForm.longitude = result.detail.location.lng;
						   // 
						   let data = that.ruleForm;
						   let tjurl='v1/addpcClassroom';
						   if (that.$route.query.classid!=undefined) {
							   tjurl='v1/setpcClassroom';
							   data.cla_id = that.$route.query.classid;
						   }else{
							  data.mem_id = that.$route.query.id; 
						   };
						   that.$post(tjurl,data).then((response) => {
							console.log('',response);
							if (response.code==1000) {
								that.$message({
								  message: '',
								  type: 'success'
								});
								that.$router.go(-1);
							};
						   })
						});	
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			returnfomr(){
				this.$router.go(-1);
			},
			ObjectCon(){
				let that =this;
				let data={
					cla_id:this.$route.query.classid
				};
				that.$post('v1/getpcClassroomdetails',data).then((response) => {
					console.log('',response);
					if (response.code==1000) {
						that.ruleForm = response.data
					};
				});  
			},
			// 
			fenobj(){
				let that =this;
				let data={};
				that.$post('v1/getpcClassroomrTypelist',data).then((response) => {
					// console.log('',response);
					if (response.code==1000) {
						that.classFL = response.data
					};
				});
			}
		},
		mounted() {
			this.fenobj();
		    //id
			if (this.$route.query.classid!=undefined) {
				this.ObjectCon();
			};
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con-tit {
			padding: 20px;
			color: @color4;
			border-bottom: @borderbox;
		}

		.InBoxBody-con {
			width: calc(100% - 200px);
			padding-left: 50px;
			.InBoxBody-con-input {
				width: 100%;
				overflow: hidden;
				padding: 0 0 20px 0;
				margin-top:40px;
				.InBoxBody-con-input-style {
					width: calc(100% - 40px);
					padding: 20px 20px 0 20px;
					overflow: hidden;

					.InBoxBody-con-input-tit {
						float: left;
						line-height: 32px;
						font-size: 14px;
						width: 90px;
					}

					.flat {
						float: left;
						width: 200px;
						margin-right: 40px;
					}

					.flattextarea {
						float: left;
						width: 70%;
						margin-right: 40px;
					}
				}
			}
		}
	}
</style>



