<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text"></span>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span></span>
				<el-input class="input" placeholder=""></el-input>
				<el-select class="input" v-model="scinput.apply_type" clearable  placeholder="">
				    <el-option
				      v-for="item in optionsnl"
				      :key="item.id"
				      :label="item.name"
				      :value="item.id">
				    </el-option>
				</el-select>
				<el-button class="time" type="warning" plain @click="scIN()"> </el-button>
			</div>
			<!--  -->
			 <el-table ref="multipleTable" :data="tableData" style="width: 100%" stripe tooltip-effect="dark" :header-cell-style="{background:'#eef1f6',color:'#606266'}">
			    <el-table-column fixed prop="cname" label="" align="center"></el-table-column>
				<el-table-column prop="cuname" label="" align="center"></el-table-column>
				<el-table-column prop="snname" label="" align="center"></el-table-column>
				<el-table-column prop="clname" label="" align="center"></el-table-column>
				<el-table-column prop="caname" label="" align="center"></el-table-column>
				<el-table-column prop="c_num" label="" align="center"></el-table-column>
				<el-table-column prop="present_price" label="" align="center"></el-table-column>
				<el-table-column prop="original_price" label="" align="center"></el-table-column>
				<el-table-column prop="start_time" label="" align="center"></el-table-column>
				<el-table-column  label="" align="center">
					<template slot-scope="scope" >
						<p style="color:#67C23A;" v-if="scope.row.ismember==1"></p>
						<p style="color:#F56C6C" v-if="scope.row.ismember==2"></p>
					</template>
				</el-table-column>
				<el-table-column label="" width="320" fixed="right" align="center">
				  <template slot-scope="scope" >
					<el-button
					    size="mini"
					    type="success"
						v-if="scope.row.apply_type==2"
					    @click="handledetails(scope.$index,scope.row)"></el-button>
					<el-button
					    size="mini"
					    type="primary"
						v-if="scope.row.apply_type==2"
					    @click="handleEdit(scope.$index,scope.row)"></el-button>
					<el-button
					  size="mini"
					  type="warning"
					  v-if="scope.row.apply_type==1"
					  @click="handleOK(2,scope.$index,scope.row)"></el-button>
					<el-button
					  size="mini"
					  v-if="scope.row.apply_type!=3"
					  @click="handleOK(3,scope.$index,scope.row)"></el-button>
					<p style="color:#F56C6C" v-if="scope.row.apply_type==3"></p>
				  </template>
				</el-table-column>
			 </el-table>
			 <div class="block pagination">
			    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
				:current-page.sync="scinput.page" :page-size="16" layout="prev, pager, next, jumper" :total="num">
				</el-pagination>
			 </div>
		</div>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  optionsnl:[
			  {id:'',name:''},
			  {id:1,name:''},
			  {id:2,name:''},
			  {id:3,name:''},
		  ],
		  valueTIME:'',
		  //
		  num:1,
		  //id
		  classid:null,
		  //
		  dataobj: {},
		  //
		  scinput:{
			  name:'',
			  apply_type:'',
			  page:1
		  },
		  tableData: [],
	    }
	  },
	  methods: {
		// 
		handleEdit(index, row){
			this.$storeState.HCommcourse = this.form;
			this.$router.push({path:'CompList/CompListCon',query:{mid:row.id}});
		},
		//
		handledetails(index, row) {
			this.$storeState.HCommcourse = this.form;
			this.$router.push({path:'CompList/CompListD',query:{mid:row.id}});
		},
		//
		scIN(){
			this.ObjAjax();
		},
		handleSizeChange(val) {
			console.log(` ${val} `);
			this.scinput.page = val;
			//
			this.ObjAjax();
		},
		handleCurrentChange(val) {
			console.log(`: ${val}`);
			this.scinput.page = val;
			//
			this.ObjAjax();
		},
		//
		handleOK(state,index, row) {
			console.log(state,index, row);
			let textname='';
			if (state==2) {
				textname=''
			};
			this.$confirm(''+textname+''+row.cname+', ?', '', {
			  confirmButtonText: '',
			  cancelButtonText: '',
			  type: 'warning'
			}).then(() => {
				let that =this;
				let data={
					syntheticalscourse_id:row.id,
					type:state
				};
				that.$post('v1/setpcSyntheticalCourse',data).then((response) => {
					console.log('',response);
					if (response.code==1000) {
						that.$message({
							type: 'success',
							message: '!'
						});
						//
						that.ObjAjax();
					};
				}); 
			}).catch(() => {
			  this.$message({
				type: 'info',
				message: ''
			  });          
			});
		},
		//
		ObjAjax(){
			let that =this;
			let data=this.scinput;
			that.$post('v1/getpcSynthetical',data).then((response) => {
				console.log('',response);
				if (response.code==1000) {
					response.data.info.forEach(obj=>{
						obj.snname = that.moenystate(obj.snname);
						obj.clname = that.moenystate(obj.clname);
						if (obj.start_time==null) {
							obj.start_time = that.moenystate(obj.start_time);
						} else{
							obj.start_time = that.$timeZH(obj.start_time);
						}
					});
					this.tableData = response.data.info;
					this.num = response.data.num;
				};
				if (response.error_code==10000) {
					that.tableData=[];
				};
			}); 
		},
		// null-
		moenystate(e){
			if(e==null){e='-'}
			return e
		},
	  },
	  mounted() {
	  	this.ObjAjax();
	  },
	  created(){
	  		//
	  		if (this.$storeState.CompList!=null) {
	  			this.form = this.$storeState.CompList;
	  		};  
	  }
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//
	.InBoxBody{
		width: 100%;
		border:@borderbox;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;	
		overflow: hidden;
		.InBoxBody-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.InBoxBody-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
		}
		.InBoxBody-con{
			width: 100%;
			overflow: hidden;
			.InBoxBody-con-input{
				width: 100%;
				overflow: hidden;
				margin-bottom:20px;
				span{
				   margin-left: 20px;
				   color: @color4;
				   font-size: 16px;
				   float: left;
				   line-height: 32px;
				};
				.input{
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
				};
				.time{
					float: left;
					height: 32px;
					margin-left: 12px;
				}
				.buttonRight{
					float: right;
					margin-right: 30px;
				}
			};
			.pagination{
				float: right;
				margin: 20px;
			}
			.logo{
				width: 50px;
				height: 50px;
				display: block;
				float: left;
				margin-top: 4px;
			}
			.logours{
				float: left;
				margin-left: 10px;
				width:80px;
				text-align: left;
				div{
					line-height: 60px;
				}
			}
		}
	}
	
	.avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 120px;
	    height: 120px;
	    line-height: 120px;
	    text-align: center;
		border: 1px dashed #d9d9d9;
	  }
	  .avatar {
	    width: 120px;
	    height: 120px;
	    display: block;
	  }
	 .dialoginput{
		 width: 100%;
		 margin: 20px 0;
		 overflow: hidden;
		 .dialoginputFl{
			 width:calc(50% - 10px);
			 margin-right: 10px;
			 float: left;
		 }
	 }
</style>



